export default {
	data() {
		return {
			customerList: [],
			num: 0,
		};
	},
	onLoad() {
		let mid = this.getMerchantInfo().id;
		if(mid){
			this.getCustomerList();
		}else{
			uni.showToast({
				title:"您还未认证商家",
				icon:"none"
			})
			setTimeout(()=>{
				uni.switchTab({
					url:"../tabbar/data"
				})
			},1000)
		}
	},
	methods: {
		getCustomerList() {
			uni.request({
				url: this.prefix + "/getCustomerList",
				data: {
					mid: this.getMerchantInfo().id
				},
				success: (res) => {
					if (res.data.code === 200) {
						this.customerList = res.data.data
						console.log(this.customerList)
					} else {
						uni.showToast({
							title: "获取k客户异常",
							icon: "none"
						})
					}
				}
			})
		}
	}
}
